import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

import Promotion from './Promotion'
import { Modal } from '../ui-components'

const SpecialOfferButton = styled.button`
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	padding: 8px;
	text-align: center;
	border: none;
	background-color: #e76b6b;
	color: white;
	overflow: hidden;

	div {
		position: relative;

		&:before {
			width: 0;
			height: 0;
			position: absolute;
			right: calc(50% - 80px);
			content: '';
			border-top: 10px solid transparent;
			border-right: 20px solid white;
			border-bottom: 10px solid transparent;

			animation: slideleft 1s ease-out infinite;
		}

		&:after {
			width: 0;
			height: 0;
			position: absolute;
			left: calc(50% - 80px);
			content: '';
			border-top: 10px solid transparent;
			border-left: 20px solid white;
			border-bottom: 10px solid transparent;

			animation: slideright 1s ease-out infinite;
		}
	}

	@keyframes slideleft {
		from {
			transform: translateX(20px);
			opacity: 0.2;
		}
		to {
			transform: translateX(0);
			opacity: 1;
		}
	}

	@keyframes slideright {
		from {
			transform: translateX(-20px);
			opacity: 0.2;
		}
		to {
			transform: translateX(0);
			opacity: 1;
		}
	}
`

const PromotionModal = () => {
	const [ show, setShow ] = useState(false);

	const handleShow = () => setShow(true);
	const handleClose = () => setShow(false);

	return (
		<>
			<SpecialOfferButton onClick={handleShow}>
				<div>Special Offer!</div>
			</SpecialOfferButton>
			<Modal show={show} onHide={handleClose} centered>
				<Modal.CloseBtn onClick={handleClose}>&times;</Modal.CloseBtn>
				<Promotion />
			</Modal>
		</>
	);
}
 
export default PromotionModal;